@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

body {
   background-color: #DFCFBE;
}

/*    DISPLAY SHOW    */
.display-show {
   position: absolute;
   top: 150px;
   left: 50%;
   transform: translateX(-50%);
   display: flex;
   /* background-color: black; */
}

.like-button {
   margin: 120px 30px;
   width: 120px;
   height: 120px;
   border-radius: 70px;
   box-shadow: 5px 5px 6px 0px rgba(0,0,0,0.4);
}
.like-button:hover {
   cursor: pointer;
   box-shadow: 4px 4px 10px 3px rgba(0,0,0,0.4);
}

.click-animation {
   animation: click 1s forwards;

}
@keyframes click {
   from {
      transform: rotateX(0deg);
   }
   to {
      transform: rotateX(359deg);
   }
}

.cards-container {
   position: absolute;
   left: 50%;
   transform: translateX(-50%);
   width: 80%;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
}
.cards-container > div {
   margin: 20px;
}

.card {
   width: 240px;
   height: auto;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   padding: 5px 0;
   border-radius: 10px;
   background-color: whitesmoke;
   box-shadow: 10px 10px 6px 0px rgba(0,0,0,0.4);
   /* transition: 1.2s ease; */
   animation: appear 1s ease;
}
@keyframes appear {
   from { opacity: 0; }
   to { opacity: 1; }
}

.card > img:hover {
   cursor: pointer;
}

.title {
   display: flex;
   width: 90%;
   height: 85px;
   align-items: center;
   justify-content: center;
   text-align: center;
   font-size: 28px;
}
.year {
   margin-top: 5px;
   font-size: 15px;
}
.rating {
   position: relative;
   top: 8px;
   left: 18px;
   margin-bottom: 20px;
   font-size: 20px;
}
.rating-image {
   position: relative;
   top: 7px;
   width: 40px;
}


/*   TITLE, ERROR MESSAGE    */
.list-title {
   font-size: 30px;
   margin: 30px 0;
   font-family: 'Lobster', cursive;
   text-align: center;
   text-decoration: underline;
}

.error-message {
   position: absolute;
   top: 20%;
   left: 50%;
   transform: translateX(-50%);
   font-size: 25px;
}
