@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  padding: 0;
   box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
   background-color: #DFCFBE;
}

/*    DISPLAY SHOW    */
.display-show {
   position: absolute;
   top: 150px;
   left: 50%;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
   display: -webkit-flex;
   display: flex;
   /* background-color: black; */
}

.like-button {
   margin: 120px 30px;
   width: 120px;
   height: 120px;
   border-radius: 70px;
   box-shadow: 5px 5px 6px 0px rgba(0,0,0,0.4);
}
.like-button:hover {
   cursor: pointer;
   box-shadow: 4px 4px 10px 3px rgba(0,0,0,0.4);
}

.click-animation {
   -webkit-animation: click 1s forwards;
           animation: click 1s forwards;

}
@-webkit-keyframes click {
   from {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
   }
   to {
      -webkit-transform: rotateX(359deg);
              transform: rotateX(359deg);
   }
}
@keyframes click {
   from {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
   }
   to {
      -webkit-transform: rotateX(359deg);
              transform: rotateX(359deg);
   }
}

.cards-container {
   position: absolute;
   left: 50%;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
   width: 80%;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-wrap: wrap;
           flex-wrap: wrap;
   -webkit-justify-content: center;
           justify-content: center;
}
.cards-container > div {
   margin: 20px;
}

.card {
   width: 240px;
   height: auto;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   -webkit-align-items: center;
           align-items: center;
   -webkit-justify-content: center;
           justify-content: center;
   padding: 5px 0;
   border-radius: 10px;
   background-color: whitesmoke;
   box-shadow: 10px 10px 6px 0px rgba(0,0,0,0.4);
   /* transition: 1.2s ease; */
   -webkit-animation: appear 1s ease;
           animation: appear 1s ease;
}
@-webkit-keyframes appear {
   from { opacity: 0; }
   to { opacity: 1; }
}
@keyframes appear {
   from { opacity: 0; }
   to { opacity: 1; }
}

.card > img:hover {
   cursor: pointer;
}

.title {
   display: -webkit-flex;
   display: flex;
   width: 90%;
   height: 85px;
   -webkit-align-items: center;
           align-items: center;
   -webkit-justify-content: center;
           justify-content: center;
   text-align: center;
   font-size: 28px;
}
.year {
   margin-top: 5px;
   font-size: 15px;
}
.rating {
   position: relative;
   top: 8px;
   left: 18px;
   margin-bottom: 20px;
   font-size: 20px;
}
.rating-image {
   position: relative;
   top: 7px;
   width: 40px;
}


/*   TITLE, ERROR MESSAGE    */
.list-title {
   font-size: 30px;
   margin: 30px 0;
   font-family: 'Lobster', cursive;
   text-align: center;
   text-decoration: underline;
}

.error-message {
   position: absolute;
   top: 20%;
   left: 50%;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
   font-size: 25px;
}

